import moment from 'moment'
import faker from 'faker'

export default {
  events: generateEvents()
}

function generateEvents () {
  faker.locale = 'fr'
  let arr = []
  for (var x = 0; x < 6; x++) {
    arr.push({
      players: parseInt(Math.random() * 12),
      maxPlayers: 12,
      address: {
        streetName: faker.address.streetName(),
        city: faker.address.city(),
        formatted_address: faker.fake('{{address.streetAddress}}, {{address.streetName}}, {{address.city}}, {{address.country}}')
      },
      datetime: moment().add(parseInt(Math.random() * 100), 'day').hour(parseInt(24 * Math.random())).minutes(parseInt(60 * Math.random()))
    })
  }
  return arr
}



