<template>
  <div class="hello">
    <h1>Calendrier de match at Paris</h1>
    <p>
      Everyone can sign in using Facebook and create/join a Futsal event.
      <br>
      A Futsal event has a max. number of players, an address, a datetime, and organizers contact details.
      <br>
      There is a global comment/chat section to discuss and share prior and after matches.
    </p>
    <h3>Work in progress</h3>
    <ul>
      <li>Screens mockups 30%</li>
      <li>Add match event 0%</li>
      <li>Match event details 0%</li>
      <li>Chat 0%</li>
      <li>MVP Version 10%</li>
    </ul>
    <ul v-show="false">
      <router-link to="/login">Login</router-link>
    </ul>
    <Events></Events>
  </div>
</template>

<script>
import Events from './Events'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Paris Futsal'
    }
  },
  components: {
    Events: Events
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



